.product {
  display: flex;
  padding: 4rem 0 8rem 4rem;
  background-color: #f0f0f0eb;

  &__show {
    flex: 3;
  }

  &__description {
    flex: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__content--spacing {
    padding-right: 25rem;
  }
}